
.toooltip-img {
  width: 180px;
  height: 180px;
}
.wu-shop {
  position: relative;
  display: block;
  min-height: 580px;
  max-width: 1300px;
  margin: 0 auto;
}
.wu-shop .shop-container {
  position: absolute;
  top: 100px;
  left: 20%;
  width: 420px;
}
.wu-shop .shop-container .shop-head {
  text-align: center;
}
.wu-shop .shop-container .shop-head h2 {
  font-size: 32px;
}
.wu-shop .shop-container .shop-head p {
  line-height: 40px;
}
.wu-shop .shop-container .shop-content {
  margin-left: 40px;
}
.wu-shop .shop-container .shop-content h3 {
  margin-bottom: 10px;
}
.wu-shop .shop-container .shop-content p {
  font-size: 14px;
  line-height: 26px;
}
.wu-shop .shop-container .shop-footer {
  margin: 40px;
}
.wu-shop .simulator {
  z-index: 1;
  overflow: hidden;
  position: absolute;
  border-radius: 6px;
  background: #fafafa;
  box-sizing: border-box;
  right: 100px;
  width: 320px;
  min-width: 320px;
  top: 10px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.2), 0 1px 2px rgba(0,0,0,0.2);
}
.wu-shop .simulator .simulator__nav {
  height: 60px;
  background-size: 100%;
}
.wu-shop .simulator .simulator__content {
  position: relative;
  width: 100%;
  height: 572px;
  display: block;
}
.wu-shop .simulator .preview-image {
  width: 100%;
  display: block;
  margin-top: -62px;
}
.wu-shop .simulator .preview-popup {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255,255,255,0.98);
  transition: 0.3s;
  text-align: center;
}
.wu-shop .simulator .preview-content {
  left: 0;
  top: 50%;
  width: 100%;
  height: 200px;
  position: absolute;
  text-align: center;
  transform: translateY(-60%);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.wu-shop .simulator .preview-content .img {
  width: 180px;
  height: 180px;
  margin-bottom: 15px;
  display: inline-block;
}
.wu-shop .simulator .preview-content p {
  margin: 0;
  color: #34495e;
  line-height: 1.5;
  font-size: 16px;
}
@media (max-width: 1300px) {
.wu-shop .simulator {
    width: 320px;
    min-width: 320px;
}
}


