/* normalize.css v3.0.3 | MIT License | github.com/necolas/normalize.css */
html {
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  margin: 0;
  height: 100%;
}
article,
aside,
footer,
header,
main,
menu,
nav,
section {
  display: block;
}
[hidden] {
  display: none;
}
a {
  background-color: transparent;
  text-decoration: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
img {
  border: 0;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
/* 1 */
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
/* 1 */
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
textarea {
  overflow: auto;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
input {
  border: none;
}
ul,
h2,
h3 {
  -webkit-margin-after: 0;
  -webkit-margin-before: 0;
  -webkit-padding-start: 0;
}
p {
  display: block;
  -webkit-margin-after: 1em;
  -webkit-margin-before: 1em;
  -webkit-padding-start: 0;
}
ul,
li {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
}
i,
em {
  font-style: normal;
}
.clearfix:after,
.clearfix:before {
  content: " ";
  display: table;
  zoom: 1;
}
.clearfix:after {
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}
*,
:after,
:before {
  box-sizing: border-box;
}
#app {
  height: 100%;
}
.page {
  margin-top: -29px;
  padding: 0 38px 30px;
}
.panel {
  position: relative;
  margin-bottom: 20px;
  background: #fff;
}
.panel:after {
  content: " ";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.panel .panel-title {
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #eee;
}
.panel .panel-title .title {
  display: inline-block;
  font-size: 18px;
  padding: 0 15px;
  height: inherit;
  cursor: pointer;
  color: #999;
}
.panel .panel-title .title:hover,
.panel .panel-title .title.active {
  border-bottom: 2px solid #1d8ce0;
  color: #333;
}
.direct-up {
  color: #b73712 !important;
}
.direct-down {
  color: #427f0c !important;
}
table {
  width: 100%;
  border: 0;
  background: #fff;
}
table thead th {
  background: #eaeefb;
  padding: 12px;
  vertical-align: middle;
  border-bottom: none;
  text-align: left;
  color: #5e6d82;
  font-size: 14px;
  white-space: nowrap;
}
table thead th:first-child {
  padding-left: 10px;
}
table thead th.sort {
  position: relative;
  cursor: pointer;
}
table thead th .icon {
  display: inline-block;
  position: absolute;
  right: 8px;
  top: 38%;
  cursor: pointer;
}
table tbody tr td {
  padding: 13px 20px;
  border-top: none;
  text-align: left;
  color: #5e6d82;
  font-size: 14px;
  font-weight: 400;
  border-bottom: 1px solid #e7f3fc;
}
table tbody tr td:first-child {
  padding-left: 10px;
}
table tbody tr td.blue {
  color: #20a0ff;
  cursor: pointer;
}
table tbody tr td.disabled {
  color: $color_font_default;
}
table tbody tr td .pl {
  padding-left: 10px;
}
table tbody:last-child {
  border-bottom: 1px solid #eaeefb;
}
.markdown {
  color: rgba(0,0,0,0.65);
  font-size: 14px;
  line-height: 1.8;
}
.markdown a {
  color: #108ee9;
  background: transparent;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  transition: color 0.3s ease;
}
.highlight {
  line-height: 1.5;
}
.markdown img {
  vertical-align: middle;
  max-width: 100%;
}
.markdown h1 {
  color: rgba(0,0,0,0.85);
  font-weight: 500;
  line-height: 40px;
  margin-bottom: 24px;
  margin-top: 8px;
  font-family: Lato, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
}
.markdown h1 .subtitle {
  font-weight: normal;
  font-size: 90%;
  margin-left: 12px;
}
.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6 {
  color: rgba(0,0,0,0.85);
  font-family: Lato, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  margin: 1.6em 0 0.6em;
  font-weight: 500;
  clear: both;
}
.markdown h1 {
  font-size: 28px;
}
.markdown h2 {
  font-size: 22px;
}
.markdown h3 {
  font-size: 18px;
}
.markdown h4 {
  font-size: 16px;
}
.markdown h5 {
  font-size: 14px;
}
.markdown h6 {
  font-size: 12px;
}
.markdown hr {
  height: 1px;
  border: 0;
  background: #e9e9e9;
  margin: 16px 0;
  clear: both;
}
.markdown p,
.markdown pre {
  margin: 1em 0;
}
.markdown ul > li {
  list-style-type: circle;
  margin-left: 20px;
  padding-left: 4px;
}
.markdown ul > li:empty {
  display: none;
}
.markdown ol > li {
  list-style-type: decimal;
  margin-left: 20px;
  padding-left: 4px;
}
.markdown ul > li > p,
.markdown ol > li > p {
  margin: 0.2em 0;
}
.markdown code {
  margin: 0 1px;
  background: #f7f7f7;
  padding: 0.2em 0.4em;
  border-radius: 3px;
  font-size: 0.9em;
  border: 1px solid #eee;
}
.markdown pre {
  border-radius: 2px;
  background: #f7f7f7;
  font-family: "Lucida Console", Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
}
.markdown pre code {
  border: none;
  background: #f7f7f7;
  margin: 0;
  padding: 0;
  font-size: 13px;
  color: rgba(0,0,0,0.65);
  overflow: auto;
}
.markdown strong,
.markdown b {
  font-weight: 600;
}
.markdown > table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #e9e9e9;
  width: 100%;
  margin: 8px 0 16px;
}
.markdown > table th {
  white-space: nowrap;
  color: #5c6b77;
  font-weight: 600;
}
.markdown > table th,
.markdown > table td {
  border: 1px solid #e9e9e9;
  padding: 8px 16px;
  text-align: left;
}
.markdown > table th {
  background: #f7f7f7;
}
.markdown blockquote {
  font-size: 90%;
  color: #999;
  border-left: 4px solid #e9e9e9;
  padding-left: 0.8em;
  margin: 1em 0;
}
.markdown blockquote p {
  margin: 0;
}
.markdown .anchor {
  opacity: 0;
  transition: opacity 0.3s ease;
  margin-left: 8px;
}
.markdown .waiting {
  color: #ccc;
  cursor: not-allowed;
}
.markdown a.edit-button {
  line-height: 12px;
  display: inline-block;
  margin-left: 10px;
  height: 12px;
  text-decoration: none;
}
.markdown a.edit-button i {
  color: #999;
}
.markdown a.edit-button i:hover {
  color: #666;
}
.markdown a.edit-button .anticon {
  display: block;
  font-size: 16px;
}
.markdown h1:hover .anchor,
.markdown h2:hover .anchor,
.markdown h3:hover .anchor,
.markdown h4:hover .anchor,
.markdown h5:hover .anchor,
.markdown h6:hover .anchor {
  opacity: 1;
  display: inline-block;
}
.markdown > br,
.markdown > p > br {
  clear: both;
}
.markdown.api-container table {
  font-size: 13px;
}
.markdown.api-container table td:first-child {
  background: #fcfcfc;
  font-weight: 500;
  width: 20%;
  font-family: "Lucida Console", Consolas, Menlo, Courier, monospace;
}
.markdown.api-container table td:nth-child(3) {
  width: 22%;
  font-size: 12px;
  font-family: "Lucida Console", Consolas, Menlo, Courier, monospace;
}
.markdown.api-container table td:last-child {
  width: 13%;
  font-size: 12px;
  font-family: "Lucida Console", Consolas, Menlo, Courier, monospace;
}
.markdown ul.wu-timeline li.wu-timeline-item {
  list-style: none;
  margin: 0;
  padding: 0 0 30px;
}
.markdown ul.wu-timeline li.wu-timeline-item .wu-timeline-item-content {
  font-size: 14px;
  padding-left: 32px;
  position: relative;
  top: -14px;
}
.markdown ul.wu-timeline li.wu-timeline-item .wu-timeline-item-content > h2 {
  margin-top: 0;
  padding-top: 0.5px;
}
.markdown ul.wu-timeline li.wu-timeline-item:first-child {
  margin-top: 40px;
}
ul.wu-icons-list {
  margin: 40px 0;
  list-style: none;
  overflow: hidden;
}
ul.wu-icons-list li {
  float: left;
  width: 16.66%;
  text-align: center;
  list-style: none;
  cursor: pointer;
  height: 100px;
  color: #555;
  transition: all 0.3s;
  position: relative;
  margin: 3px 0;
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
  padding: 10px 0 0;
}
ul.wu-icons-list li .wu-icon {
  font-size: 24px;
  margin: 12px 0 16px;
  transition: all 0.3s;
}
ul.wu-icons-list li .wu-icon-class {
  display: block;
  text-align: center;
  transform: scale(0.83);
  font-family: "Lucida Console", Consolas;
  white-space: nowrap;
}
ul.wu-icons-list li:hover {
  background-color: #2d8cf0;
  color: #fff;
}
ul.wu-icons-list li:hover .anticon {
  transform: scale(1.4);
}
ul.wu-icons-list li.copied:hover {
  color: rgba(255,255,255,0.2);
}
ul.wu-icons-list li:after {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  content: "Copied!";
  text-align: center;
  line-height: 110px;
  color: #fff;
  transition: all 0.3s cubic-bezier(0.18, 0.89, 0.32, 1.28);
  opacity: 0;
}
ul.wu-icons-list li.copied:after {
  opacity: 1;
  top: -10px;
}
.color-content {
  margin: 45px 0;
}
.color-content .page-color {
  display: -ms-flexbox;
  display: flex;
}
.color-content .page-color-item {
  cursor: pointer;
  -ms-flex: 1;
      flex: 1;
  height: 80px;
  border-radius: 4px;
  float: left;
  margin-right: 4px;
  transition: all 0.2s;
  position: relative;
  text-align: center;
  padding-top: 28px;
  font-family: Consolas;
  font-size: 12px;
}
.color-content .page-color-item:last-child {
  margin-right: 0;
}
.color-content .page-color-item:hover {
  margin-top: -10px;
  height: 90px;
}
.color-content .page-color-value {
  font-size: 12px;
  position: absolute;
  bottom: -4px;
  transform: scale(0.85);
  left: 0;
  width: 100%;
  text-align: center;
  opacity: 0;
  transition: all 0.2s ease 0.1s;
}
.color-content .color-title {
  margin: 0 0 24px;
  font-weight: 500;
  color: #5c6b77;
  font-size: 26px;
  text-transform: capitalize;
  text-align: center;
}
.color-content .color-description {
  font-size: 12px;
  font-weight: lighter;
  color: #777;
  display: block;
}
.color-content .page-color:hover .page-color-item {
  padding-top: 4px;
}
.color-content .page-color:hover .page-color-value {
  opacity: 0.7;
  bottom: 3px;
}
.home-page {
  min-height: 100vh;
}
.home-page .wu-btn {
  width: 110px;
  border-radius: 36px;
}
.home-page .header-navbar .header-navbar__search {
  display: none;
}
.home-page .home-page-wrapper {
  position: relative;
  top: 100px;
  width: 100%;
  padding: 0;
}
.home-page .home-page-wrapper .page {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  position: relative;
  padding: 0 24px;
}
.home-page .home-page-wrapper .text-wrapper {
  position: relative;
  left: 10%;
  width: 54%;
  max-width: 560px;
  min-width: 420px;
  min-height: 336px;
  color: #0d1a26;
  z-index: 1;
}
.home-page .home-page-wrapper .img-wrapper {
  width: 46%;
  width: 482px;
  height: 500px;
  position: absolute;
  right: 10%;
  bottom: -50px;
  background: url("./assets/images/index.png") no-repeat;
  background-repeat: no-repeat;
  background-position: 40% 80%;
  background-size: 120%;
}
.home-page__title {
  font-size: 48px;
  font-weight: 700;
  color: rgba(0,0,0,0.85);
  text-align: left;
}
.home-page__sub-title {
  margin: 20px 0 40px;
  font-size: 22px;
  text-align: left;
  color: #697b8c;
}
.home-button {
  width: 250px;
}
.home-button .btn {
  display: inline-block;
}
.home-button .btn:first-child {
  margin-right: 20px;
}


