
.header-navbar {
  transition: opacity 0.5s;
  background: #fff;
  height: 80px;
  margin-bottom: 24px;
  padding: 0 48px;
  width: 100%;
}
.header-navbar a {
  color: #666;
}
.header-navbar__logo {
  float: left;
  height: auto;
  padding: 2px 0 0;
}
.header-navbar__logo .logo {
  display: block;
  width: 200px;
  height: 79px;
  margin-top: 10px;
  background: url("../../assets/logo_wuui.png") no-repeat;
  background-size: 65%;
  opacity: 0.8;
}
.header-navbar__search {
  float: left;
  margin-top: 30px;
  width: 220px;
  padding-left: 10px;
  border-left: 1px solid #e9e9e9;
}
.header-navbar__search .wu-select {
  width: 160px;
}
.header-navbar__search .wu-select-selection {
  background: none;
  border: none;
}
.header-navbar__search .wu-select-open .wu-select-selection {
  border-color: none;
  box-shadow: none;
}
.header-navbar__search .wu-icon {
  display: none;
}
.header-navbar .header-nav {
  float: right;
  font-size: 14px;
}
.header-navbar .header-nav .wu-menu-horizontal {
  border-bottom: none;
}
.header-navbar .header-nav .wu-menu-horizontal > .wu-menu-item {
  height: 80px;
  line-height: 76px;
  min-width: 72px;
  border-top: 2px solid transparent;
}
.header-navbar .header-nav .wu-menu-horizontal > .wu-menu-item a {
  display: block;
  font-weight: 400;
}
.header-navbar .header-nav .wu-menu-horizontal > .wu-menu-item:hover {
  border-top: 2px solid #108ee9;
  border-bottom: 2px solid transparent;
}
.header-navbar .header-nav .wu-menu-horizontal > .wu-menu-item-selected {
  border-top: 2px solid #1890ff;
  border-bottom: 2px solid transparent;
}
.header-navbar .header-nav .wu-menu-horizontal > .wu-menu-item-selected a {
  font-weight: 500;
  color: #108ee9;
}
.header-navbar .header-lang {
  float: right;
  margin-top: 28px;
  margin-left: 40px;
  padding: 1px 5px 2px;
  color: #999;
  font-size: 12px;
  border-radius: 3px;
  border: 1px solid #e9e9e9;
}


